.sidebar {
    .nav-item {
        a.exact, button {
            display: flex;
            width: 100%;
            align-items: center;
            justify-content: space-between;
            overflow: hidden;
            white-space: nowrap;
            border-radius: 0.375rem;
            padding: 4px 8px 4px 12px;
            min-height: 40px;
            margin: 0px 0px 4px;

            &:hover {
                background-color: rgb(var(--color-primary-500)/0.08);
            }
            &.active{
                &:hover{
                    background-color: rgb(var(--color-primary-500)/0.16);
                }
                background-color: rgb(var(--color-primary-500)/0.08);
                color: rgb(var(--color-primary-500));
                svg {
                    color: rgb(var(--color-primary-500));
                }
            }
        }
    }

    &:hover{
        width: auto
    }


    ul.sub-menu {
        li {
            a, button {
                display: flex;
                width: 100%;
                align-items: center;
                padding: 0.3rem 1.8rem;
                justify-content: flex-start;

                &:before {
                    height: .125rem;
                    width: .5rem;
                    border-radius: .25rem;
                    content: var(--tw-content);
                    --tw-bg-opacity: 1;
                    background-color: rgb(209 213 219/var(--tw-bg-opacity));
                }

                &:hover{
                    --tw-bg-opacity: 1;
                    // background-color: rgb(243 244 246/var(--tw-bg-opacity));
                    --tw-text-opacity: 1;
                    color: rgb(67 97 238/var(--tw-text-opacity));

                    &:before{
                        content: var(--tw-content)!important;
                        --tw-bg-opacity: 1!important;
                        background-color: rgb(67 97 238/var(--tw-bg-opacity))!important
                    }
                }

                &.active{
                    --tw-text-opacity: 1;
                    // color: rgb(67 97 238/var(--tw-text-opacity));
                    color: rgb(var(--color-primary-500));

                    &:before{
                        content: var(--tw-content);
                        --tw-bg-opacity: 1;
                        background-color: rgb(var(--color-primary-500));
                    }

                    @at-root {
                        .nav-item & {
                            background-color: rgb(var(--color-primary-500)/0.08);
                        }
                    }
                }
            }
        }
    }
}

