[dir=ltr] {
    .sidebar {
        ul.sub-menu {
            li {
                a, button {
                    &:before {
                        content: var(--tw-content);
                        margin-right: .5rem 
                    }
                }
            }
        }
    }

    
    .vertical .sidebar {
        left: -260px;
    }
    .vertical.toggle-sidebar {
        @media (max-width: 1023.9px){
            .nav_overlay {
                display: block;
            }
        }
        .sidebar {
            left: 0;
        }
    }


    @media (min-width: 1024px){
        .vertical .sidebar {
            left: 0;
        }
        .main-content {
            margin-left: 260px;
        }
        .vertical {
            .sidebar {
                left: 0;
            }
            &.toggle-sidebar {
                .sidebar {
                    left: -260px;
                }
                .main-content {
                    margin-left: 0;
                }
            }
        }
    }

}


[dir=rtl] {
    .sidebar {
        ul.sub-menu {
            li {
                a, button {
                    &:before {
                        content: var(--tw-content);
                        margin-left: .5rem
                    }
                }
            }
        }
    }

    .vertical .sidebar {
        right: -260px;
    }
    @media (min-width: 1024px){
        .vertical .sidebar {
            right: 0;
        }
        .main-content {
            margin-right: 260px;
        }
        .vertical.toggle-sidebar .sidebar {
            right: -260px;
        }
        .vertical.toggle-sidebar .main-content {
            margin-right: 0;
        }
    }
}


.vertical.toggle-sidebar {
    .collapse-icon, .horizontal-logo {
        display: flex;
    }
} 


.sidebar .nav-item a div:first-child svg,.sidebar .nav-item button div:first-child svg {
    height: 1.25rem;
    width: 1.25rem;
    // color: #0e172680
}


.main-container { 
    .main-content {
        transition-property: all;
        transition-timing-function: cubic-bezier(.4,0,.2,1);
        transition-duration: .3s
    }
}

